import { Button, CircularProgress, Grid, TextField } from "@mui/material";
import React, { useContext, useState } from "react";
import { API_ENDPOINT, getShortLink } from "../utils/api";
import { AppDispatch, CONTEXT_ACTION } from "../utils/context";

export const InputForm = () => {
  const [value, setValue] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const dipsatch = useContext(AppDispatch);

  const validateLink = () => {
    return Boolean(value.match(/https?:\/\/[^\s]+/));
  };

  const handleSubmit = async () => {
    const isLinkOk = validateLink();
    if (!isLinkOk) {
      setError("Плохая ссылка");
      return;
    }

    setLoading(true);
    const result = await getShortLink(value);
    setLoading(false);

    if (!result) return setError("Ошибка в api");

    dipsatch({
      type: CONTEXT_ACTION.UPDATE_GENERATED_LINK,
      payload: `${API_ENDPOINT}my/${result.shortUrl}`,
    });

    setValue("");
    setError("");
  };

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      handleSubmit();
    }
  };

  return (
    <Grid item container alignItems={"center"} spacing={2} xs={12}>
      <Grid item xs={12}>
        <TextField
          disabled={loading}
          onKeyDown={handleKeyDown}
          fullWidth
          label={"Введите что нибудь..."}
          onChange={(e) => setValue(e.target.value)}
          value={value}
          variant="outlined"
          error={Boolean(error)}
          helperText={error}
        />
      </Grid>
      <Grid item xs={12}>
        <Button
          startIcon={loading && <CircularProgress size={"20px"} />}
          disabled={loading}
          onClick={handleSubmit}
          size="large"
          variant="contained"
        >
          {"Отправить"}
        </Button>
      </Grid>
    </Grid>
  );
};
