import { createTheme, ThemeProvider } from "@mui/material";
import React, { useContext } from "react";
import { AppStore } from "../utils/context";

export const ThemeWrapper = ({ children }) => {
  const { theme } = useContext(AppStore);
  return (
    <ThemeProvider
      theme={createTheme({
        palette: {
          mode: theme,
        },
      })}
    >
      {children}
    </ThemeProvider>
  );
};
