import { Typography } from "@mui/material";
import React from "react";

export const PageTitle = ({ title }) => {
  return (
    <Typography
      sx={{
        color: "text.primary",
        marginBottom: "100px",
        marginTop: "50px",
      }}
      textAlign={"center"}
      variant="h3"
    >
      {title}
    </Typography>
  );
};
