import { Fade, Grid, Link as MuiLink } from "@mui/material";
import React, { useContext } from "react";
import { AppStore } from "../utils/context";

export const ShowLink = () => {
  const { link } = useContext(AppStore);

  return (
    <Grid item xs={12}>
      <Fade in={true}>
        <MuiLink
          target="_blank"
          rel="noopener"
          href={link}
          sx={{ cursor: "pointer" }}
          variant="h6"
        >
          {link}
        </MuiLink>
      </Fade>
    </Grid>
  );
};
