import { Grid } from "@mui/material";
import React from "react";
import { PageTitle } from "../PageTitle";
import { StatsTable } from "../StatsTable";

export const TablePage = () => {
  return (
    <Grid container justifyContent={"center"}>
      <Grid item xs={8}>
        <PageTitle title={"Таблица"} />
      </Grid>
      <Grid item xs={8}>
        <StatsTable />
      </Grid>
    </Grid>
  );
};
