import { useContext } from "react";
import useSWR from "swr";
import { API_METHODS, getStats } from "../../utils/api";
import { AppStore } from "../../utils/context";

export const useSwrStats = () => {
  const { user } = useContext(AppStore);
  const { data } = useSWR(API_METHODS.GET_STATS, () => getStats(user), {
    isPaused: () => !user,
  });

  return { stats: data };
};
