import { createContext } from "react";

export const initialContext = {
  theme: "dark",
  link: "",
  user: null,
};

export const CONTEXT_ACTION = {
  TOGGLE_THEME: "toggleTHeme",
  UPDATE_GENERATED_LINK: "updateGeneratedLink",
  UPDATE_USER: "updateUser",
};

export const appReducer = (state, { type, payload }) => {
  switch (type) {
    case CONTEXT_ACTION.TOGGLE_THEME:
      return { ...state, theme: payload };
    case CONTEXT_ACTION.UPDATE_GENERATED_LINK:
      return { ...state, link: payload };
    case CONTEXT_ACTION.UPDATE_USER:
      return { ...state, user: payload };
    default:
      throw new Error("No such action");
  }
};

export const AppStore = createContext(initialContext);
export const AppDispatch = createContext();
