import axios from "axios";

export const API_ENDPOINT =
  process.env.NODE_ENV === "https://archappyurl.herokuapp.com/";

export const API_METHODS = {
  GET_SHORT_LINK: "shorten",
  GET_STATS: "admin/stat",
};

export const callApi = axios.create({
  baseURL: API_ENDPOINT,
  timeout: 1000,
});

export const getShortLink = async (url) => {
  try {
    const result = await callApi({
      method: "POST",
      url: API_METHODS.GET_SHORT_LINK,
      data: {
        url: url,
      },
    });
    return result.data;
  } catch (error) {
    console.log(error);
    return null;
  }
};

export const getStats = async ({ username, password }) => {
  try {
    const result = await callApi({
      method: "GET",
      url: API_METHODS.GET_STATS,
      auth: {
        username,
        password,
      },
    });
    return result.data;
  } catch (error) {
    console.log(error);
    return null;
  }
};

export const checkLogin = async (username, password) => {
  try {
    const result = await callApi({
      method: "GET",
      url: API_METHODS.GET_STATS,
      auth: {
        username,
        password,
      },
    });
    return result.status === 200;
  } catch (error) {
    console.log(error);
    return false;
  }
};
