import React, { useReducer } from "react";
import {
  AppDispatch,
  appReducer,
  AppStore,
  initialContext,
} from "../utils/context";

export const ContextWrapper = ({ children }) => {
  const [appState, dispatch] = useReducer(appReducer, initialContext);

  return (
    <AppStore.Provider value={appState}>
      <AppDispatch.Provider value={dispatch}>{children}</AppDispatch.Provider>
    </AppStore.Provider>
  );
};
